html {
  min-height: 100%;
}

body {
  margin: 0;
  padding: 0;
  font-family: sans-serif;
  background: url(/img/mario-bg.png) no-repeat;
  background-size: 100%;
  background-position: bottom;
  background-color: #95e8f3;
  min-height: 100%;
}

form {
  padding: 40px;
  margin: 60px 50px 60px 50px;
}

form button,
form h5 {
  margin: 20px 0;
}

input[type=text]:not(.browser-default):focus:not([readonly]),
input[type=email]:not(.browser-default):focus:not([readonly]),
input[type=password]:not(.browser-default):focus:not([readonly]),
textarea.materialize-textarea:focus:not([readonly]) {
  border-color: #ec4040;
  box-shadow: none;
}

input[type=text]:not(.browser-default):focus:not([readonly])+label,
textarea.materialize-textarea:focus:not([readonly])+label,
input[type=email]:not(.browser-default):focus:not([readonly])+label,
input[type=password]:not(.browser-default):focus:not([readonly])+label {
  color: #ec4040 !important;
}

.sign-in,
.sign-up {
  margin: 0 50px;
}
.logo-wrapper-div{
  position: relative;
}
.App-icon-plus{
  height: 66px;
  width: 18px;
  left: 185.3px;
  position: absolute;
}
.App-logo-react {
  -webkit-animation: animate-spin-react infinite 10s linear;
          animation: animate-spin-react infinite 10s linear;
  height: 63px;
  width: 52px;
  left: 140px;
  position: absolute;
}

.App-logo-redux {
  -webkit-animation: animate-spin-redux infinite 5s linear;
          animation: animate-spin-redux infinite 5s linear;
  height: 63px;
  width: 38px;
  left: 205px;
  position: absolute;
}

@-webkit-keyframes animate-spin-react {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}

@keyframes animate-spin-react {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}
@-webkit-keyframes animate-spin-redux {
  from {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
  to {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
}
@keyframes animate-spin-redux {
  from {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
  to {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
}
